import React from "react";
import utils from "../../functions/utils";
import Sprite from "../../components/core/Sprite";
import { useLocation } from 'react-router-dom';
import "./Sidenav.scss";


function TasksSide(){

    // render
    return (
        <div className="lds-sidenav-tabs">
            <Sprite.Button debug={debug} to="/tasks" />
            <Sprite.Button debug={debug} to="/tasks/grid" />
            <Sprite.Button debug={debug} to="/tasks/auto" />
        </div>
    );
}


// --- main --- //

const debug = 0;

const Sides = {
    "tasks-grid": TasksSide,
}


export default function Sidenav(props) {
    const {children} = props;
    const location = useLocation();
    const type = location?.pathname?.split('/').join("-").substring(1, 100); // remove beginning dash


    console.log("pathname: " + type);
    const SideComp = Sides[type] || TasksSide;

    // render
    const css = utils.classNames("lds-sidenav is-type-" + type);
    return (
        <div className={css} >
            <div className="lds-sidenav-left">
                <SideComp />
            </div>
            <div className="lds-sidenav-right">{children}</div>
        </div>
    );
}