import React, { useEffect } from "react";
import {Sprite, Icon, Text, Input} from "../../components/core";
import { useGlobals, useDispatch } from '../../app/context';
import Loader from "../../components/core/Loader";
import Modal from "./TasksEditModal";
import "./Tasks.scss";

const debug = 0;

export default function Screen(){
    const dispatch = useDispatch();

    const onCTA = () => dispatch({call: "SHOW_MODAL", component: <Modal />, }); // ApptModal

    // render
    return (
        <div id="lds-tasks-container">

            <Loader type="preload" duration={2000} style={{top: 70, left: 280, width: 1050, height: 800, }} />


            <Sprite.Sidenav debug={debug} id="lds-tasks-sidenav">
                <Sprite.Button debug={debug} to="/tasks/grid" style={{top: 20, left: 100, width: 80, height: 40, }} />
                <Sprite.Button debug={debug} to="/tasks/auto" style={{top: 20, left: 180, width: 80, height: 40, }} />
                <Sprite.Button debug={debug} onClick={onCTA} style={{top: 80, width: "100%", height: 40, }} />
                <Sprite.Button debug={debug} style={{top: 190, width: "100%", height: 310, }} />
            </Sprite.Sidenav>


            <Sprite.Split id="lds-tasks">
                <Sprite.Left />
                <Sprite.Right />
            </Sprite.Split>


        </div>
    );
}


