import { useGlobals, useDispatch } from '../../app/context';

const DEBUG = 0; // debug

export default function Page(){
    const dispatch = useDispatch();

    const onCTAClick = () => {
        dispatch({call: "SHOW_MODAL", component: <NewModal />});
    }

    return (
        <React.Fragment>
            <Loader fadeIn={false} open={true} duration={1000} style={{left: 280, width: 1020, }} />
            <Sprite.Container id="lds-messages-send">
                <Sprite.Button to="/messages" debug={DEBUG} style={{top: 15, left: 14, width: 80, height: 50, }} />
                <Sprite.Button debug={DEBUG} style={{top: 84, left: 24, width: 220, }} onClick={onCTAClick} />
                <Sprite.Button debug={DEBUG} style={{top: 143, left: 0, width: 270, height: 240, }} />
            </Sprite.Container>
        </React.Fragment>

    )
}